body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: cadetblue;
}

#root{
  /* display: flex; */
  /* align-items: center; */
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
input{
  border: 2px solid green;
    border-radius: 15vh;
    width: 45%;
    margin-left:10vh;
    height: 5vh;
}
.center-div{
  background-color: azure;
  width: 25%;
  border: 2px solid rgba(0, 208, 255, 0.331);
  border-radius: 4vh;
  margin-left: 35%;
  margin-top: 10%;
}
h1{
  background-color: green;
  color: aliceblue;
 
}
button{
  border-radius: 50%;
  color: cadetblue;
  border: 2px solid green;
  margin-left: 2%;
  height: 5vh;
  width: 5vh;
}
/* button:hover{
  transform: scale(1.5);
} */
.center-div h1{
  text-align: center;
}
.ToDoList{
  display: flex;
}

li{
  list-style: none;
  margin-left: 2vh;
}
#Delete:hover{
  background-color: red;
  transform: scale(1.5);
}

@media only screen and (max-width: 460px) and (min-width: 322px)  {
  .center-div{
    width: 90%;
    margin-left: 5%;
  }
  /* input{
    height: 7vh;
  }*/
  button{
    height: 8vh;
    width: 8vh;
  } 
}